<template>
  <div id="container" ref="chart" class="wm-interval_body"></div>
</template>

<style scoped>
.wm-interval_body{width: 100%; height: 100%;}
</style>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import { Chart } from '@antv/g2'

@Options({
  props: {
    type: String,                                  // : dodgestack
    value: Array<any>,                             // : [{label: 'n1', value: 0.4}, {label: 'n2', value: 0.6}]
    theme: {type: String, default: 'classic'},     // : classicacademyclassicDark
    color: {type: String, default: 'label'},       // 
    height: {type: Number, default: 0},            // 
    unit: {type: String, default: ''},             // 
  }
})
export default class Interval extends Vue {

  // 
  type!: String;
  value!: Array<any>;
  theme!: String;
  color!: String;
  height!: Number;
  unit!: String;

  private auto_height: Number = 0;

  /*  */
  created(): void {
    // 
    this.$watch('$props', (props:any)=>{
      // 
      this.init();
    }, { deep: true });
  }

  /*  */
  mounted(): void {
    // 
    this.init();
  }

  /*  */
  init(): void {
    this.$nextTick(()=>{
      // 
      let obj: any = this.$refs.chart;
      if(!this.auto_height) this.auto_height=obj.offsetHeight;
      const config: any = {
        container: obj,
        autoFit: true,
        forceFit: true,
        theme: this.theme,
      };
      if(this.height) config.height=this.height;
      const chart = new Chart(config);
      // 
      chart.interval().data(this.value)
      .encode('color', this.color)
      .encode('x', (d: any)=>d.label)
      .encode('y', (d: any)=>d.value)
      .style('radiusTopLeft', 4)
      .style('radiusTopRight', 4)
      .style('maxWidth', 80);
      // 
      chart.render();
    });
  }

}
</script>