<template>
  <div :style="{width: width, height: height}">
    <VueDatePicker
      v-model="date"
      :format="format"
      :range="range"
      :placeholder="placeholder"
      :locale="language"
      :day-names="dayName"
      :enable-time-picker="isTime"
      :text-input="isText"
      :multi-calendars="isMulti"
      :min-date="minDate"
      :max-date="maxDate"
      auto-apply
    ></VueDatePicker>
  </div>
</template>

<style lang="less">
.dp__input{vertical-align: middle; height: 40px;}
</style>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css';
@Options({
  components: { VueDatePicker },
  props: {
    value: {default: ''},                                     // : 2024-01-01[2024-01-01,2024-01-31]
    range: {type: Boolean, default: false},                   // 
    format: {type: String, default: 'yyyy/MM/dd'},            // : yyyy-MM-dd HH:mm:ss
    isTime: {type: Boolean, default: false},                  // 
    isText: {type: Boolean, default: true},                   // 
    isMulti: {type: Boolean, default: false},                 // 
    minDate: {type: String, default: ''},                     // 
    maxDate: {type: String, default: ''},                     // 
    width: {type: String, default: '100%'},                   // 
    height: {type: String, default: '40px'},                  // 
    placeholder: {type: String, default: ''},       // 
    language: {type: String, default: 'zh-CN'},               // 
    dayName: {type: Array, default: ['', '', '', '', '', '', '']},
  }
})
export default class DatePicker extends Vue {

  // 
  value!: any;
  range!: boolean;
  format!: string;
  isTime!: boolean;
  isText!: boolean;
  isMulti!: boolean;
  minDate!: string;
  maxDate!: string;
  width!: string;
  height!: string;
  placeholder!: string;
  language!: string;
  dayName!: Array<string>;
  // 
  date: string='';

  /*  */
  created(): void {
    // 
    this.$watch('value', (val:any)=>{
      this.date = val;
    }, { deep: true });
  }

}
</script>