<template>
  <div style="position: relative; width: 100%; height: 100%;" :style="{visibility:show?'inherit':'hidden'}">
    <div ref="PopupBG" class="wm-popup_bg" :style="{backgroundColor:'rgba(0,0,0,'+opacity+')'}" @click="_clickBG"></div>
    <div ref="PopupBody" class="wm-popup_body" :style="{width:width, height:height, backgroundColor:bgColor}">
      <slot></slot>
    </div>
  </div>
  </template>
  
  <style scoped>
  .wm-popup_bg,.wm-popup_body{position: absolute; z-index: 99;}
  .wm-popup_bg{width: 100%; height: 100%; top: 0; left: 0; opacity: 0;}
  .wm-popup_body{opacity: 0;}
  </style>
  
  <script lang="ts">
  import { Options, Vue } from 'vue-class-component';
  
  @Options({
    props: {
      show: {type: Boolean, default: false},          // 
      position: {type: String, default: 'center'},    // : centerleftrighttopbottom
      width: {type: String, default: ''},             // 
      height: {type: String, default: ''},            // 
      opacity: {type: Number, default: 0.4},          // 
      bgColor: {type: String, default: ''},           // 
      bgClose: {type: Boolean, default: true},        // 
      time: {type: Number, default: 400},             // 
    }
  })
  export default class Popup extends Vue {
  
    // 
    show!: boolean;
    position!: string;
    width!: string;
    height!: string;
    opacity!: number;
    bgColor!: string;
    bgClose!: boolean;
    time!: number
  
    /*  */
    created(): void {
      // 
      this.$watch('$props', (props:any)=>{
        // 
        this._animation(props.show);
      }, { deep: true });
    }
  
    /*  */
    mounted(): void {
      this.init();
    }
  
    /*  */
    init(): void {
      const bg: any = this.$refs.PopupBG;
      const body: any = this.$refs.PopupBody;
      // 
      bg.style.opacity = 0;
      if(this.position=='left'){
        body.style.height = '110%';
        body.style.left = 0;
        body.style.opacity = 1;
        body.style.transform = 'translate(-110%,0)';
      }else if(this.position=='right'){
        body.style.height = '100%';
        body.style.right = 0;
        body.style.opacity = 1;
        body.style.transform = 'translate(110%,0)';
      }else if(this.position=='top'){
        body.style.width = '100%';
        // body.style.left = 0;
        body.style.opacity = 1;
        body.style.transform = 'translate(0,-110%)';
      }else if(this.position=='bottom'){
        body.style.width = '100%';
        body.style.left = 0;
        body.style.bottom = 0;
        body.style.opacity = 1;
        body.style.transform = 'translate(0,110%)';
      }else{
        body.style.left = '50%';
        body.style.top = '50%';
        body.style.opacity = 0;
        body.style.transform = 'translate(-50%,-50%)';
      }
    }
  
    /*  */
    private _showBody(): void {
      const bg: any = this.$refs.PopupBG;
      const body: any = this.$refs.PopupBody;
      // 
      bg.style.transitionDuration = this.time+'ms';
      body.style.transitionDuration = this.time+'ms';
      // 
      bg.style.opacity = 1;
      body.style.opacity = 1;
      if(this.position=='left'){
        body.style.transform = 'translate(-1px,0)';
      }else if(this.position=='right'){
        body.style.transform = 'translate(1px,0)';
      }else if(this.position=='top'){
        body.style.transform = 'translate(0,-1px)';
      }else if(this.position=='bottom'){
        body.style.transform = 'translate(0,1px)';
      }else{
        body.style.top = '50%';
      }
      // 
      this.$nextTick(()=>{
        this.$emit('update:show', true);
      });
    }
  
    /*  */
    private _hideBody(): void {
      const bg: any = this.$refs.PopupBG;
      const body: any = this.$refs.PopupBody;
      // 
      bg.style.transitionDuration = this.time+'ms';
      body.style.transitionDuration = this.time+'ms';
      // 
      bg.style.opacity = 0;
      body.style.opacity = 0;
      if(this.position=='left'){
        body.style.transform = 'translate(-110%,0)';
      }else if(this.position=='right'){
        body.style.transform = 'translate(110%,0)';
      }else if(this.position=='top'){
        body.style.transform = 'translate(0,-110%)';
      }else if(this.position=='bottom'){
        body.style.transform = 'translate(0,110%)';
      }else{
        body.style.top = '30%'
      }
      // 
      this.$nextTick(()=>{
        this.$emit('update:show', false);
      });
    }
  
    /*  */
    private _animation(show: boolean): void {
      if(show) this._showBody();
      else this._hideBody();
    }
  
    /*  */
    _clickBG(): void {
      if(this.bgClose) this._animation(false);
    }
  
    /*  */
    public close(): void {
      this._animation(false);
    }
  
  }
  </script>
  